body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav, footer {
  padding: 0 1rem;
}

.content {
  min-height: calc(100vh - 64px - 70px);
  padding: 1rem 0;
}

.goods {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card {
  display: flex;
  flex-direction: column;
}

.card-content {
  flex-grow: 1;
}

.good-price {
  font-size: large;
  font-weight: 600;
}

.btn-small {
  background-color: #ff6d00;
}

.btn-small:hover {
  background-color: #ffd180;
  color: #000;
}

.cart-icon {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 48px;
  width: 48px;
  padding: .5rem;
  position: fixed;
  right: 2rem;
  top: calc(2rem + 64px);
  cursor: pointer;
}

.cart-icon i {
  color: #fff;
}

.cart-quantity {
  color: #fff;
}
